#page-loading {
  transition: all ease 0.5s;
  &.ng-enter-active {
    opacity: 1;
  }
  &.ng-leave-active {
    opacity: 0;
  }
}
#sidebar {
  background: #eee;
  width: 170px;
  transition: all ease 0.5s;

  #logo {
    padding: 10px;
    img {
      width: 100%;
    }
  }

  .toggle {
    width: 20px;
    height: 50px;
    background: $green;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    border-radius: 5px 0 0 5px;
    cursor: pointer;

    .sidebar.icon {
      color: #fff;
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      left: 2px;
      height: 19px;
    }
  }

  &.hidden {
    width: 50px;

    .item {
      text-align: center;
      img {
        float: none;
      }
    }
  }

  .item {
    padding: 10px;
    &.active {
      background: #ccc;
    }
    a {
      color: #333;

      img {
        height: 20px;
        width: 20px;
        float: left;
        margin-right: 5px;
      }
    }
  }

}

#feed-stream {
  transition: all ease 0.5s;
  margin-left: 180px;
  margin-bottom: 100px;
  position: relative;

  &.wider {
    margin-left: 60px;
  }
  .contain {
    background: none;
    box-shadow: none;
    border: none;

    .icon {
      float: right;
    }
  }

  .right {
    .column {
      display: flex;
    }
  }

  .article, .article.ui.fluid.card {
    background: #fff;
    padding: 25px;
    margin: 10px 0;
    border-bottom: 6px solid #ccc;
    border-radius: 0;
    transition: all ease 1s;

    &.incoming {
      background: #fff url('http://semantic-ui.com/images/wireframe/short-paragraph.png');
      background-size: 80%;
      background-repeat: no-repeat;
      background-position: center;
      height: 400px;
    }

    .actions {
      float: left;
    }
  }

  img {
    max-width: 100%;
    height: auto;
  }

  .mason {
    float: left;
  }
  .mason-sizer, .mason {
    width: calc(20% - 10px);

    &.featured, .mason-featured {
      width: calc(100% - 10px);
    }
    @include mediaWidth(1200px) {
      width: calc(50% - 10px);
    }
    @include mediaWidth(800px) {
      width: calc(100% - 10px);
    }
  }
  .mason-gutter {width: 10px; }

  .list-article {
    background: #fff;
    padding: 2px 10px;
    margin: 5px 0;

    .actions {
      float: right;
      margin: 10px;
    }

    h2.ui.header {
      font-size: 1.2em;
      margin: 5px 0;
    }
  }
}

.featured-article {
  .label.ui.ribbon {
    left: -3.3em;
  }
}

#load-more {
  margin: 15px auto;
  display: block;
}

#utility-bar {
  position: fixed;
  bottom: 0;
  height: 45px;
  background: #333;
  left: 0;
  width: 100%;
  z-index: 999;
  //padding: 7px;
  color: #fff;

  .line {
    line-height: 3.1em;
    float: left;
    margin-left: 15px;
  }

  button,a.button {
    height: 26px;
    line-height: 0.40em;
    font-size: 0.9em;
    margin-top: 0.7em;
    margin-right: 1em;
  }
  span.name {
    color: #fff;
    line-height: 2.1em;
    margin-left: 10px;
  }
  #logout-avatar {
    height: 35px;
    margin: 5px;

    .ui.label {
      background: $green;
    }

    .avatar {
      background: #eee;
      height: 25px;
      width: 25px;
      border-radius: 50%;
      float: left;
      text-align: center;
      line-height: 1.9em;
      text-transform: uppercase;
    }
  }


  #user-manage-menu {
    cursor: pointer;
    width: 50px;
    float: left;
    background: #444;
    height: 45px;
    padding: 7px;
    &:hover {
      background: #444;
    }
    .setting.icon {
      font-size: 1.8em;
      line-height: 1.2em;
      margin-left: 3px;
    }
  }

  .ui.slider.checkbox {
    input:checked~.box:before, input:checked~label:before, .box:before, label:before {
      background-color: $green !important;
    }

    padding: 5px 3px 4px 13px;
  }
}
.popout {
  cursor: pointer;
  color: #fff;
  padding: 15px;
  position: fixed;
  bottom: 45px;
  background: #333;
  height: 100px;
  left: 0;
  width: 200px;
  transition: all ease 0.5s;
  z-index: 999;

  &.ng-hide {
    bottom: -300px;
  }
}

#incoming {
  position: fixed;
  width: 100%;
  z-index: 999;
  left: 0;
  background: $green;
  color: #fff;
  padding: 10px;
  top: -45px;
  transition: all ease 0.5s;

  p {
    float: left;
    margin-right: 10px;
  }
  &.active {
    top: 0;
  }
}
